import React, {useEffect, useState} from 'react';
import {MainPage} from "../pages/MainPage/MainPage";
import {Form} from "../shared/Form/Form";
import cls from "../pages/MainPage/MainPage.module.scss"
import minus from "../icons/minus.svg"
import menu from "../icons/menu.svg"
import record from "../icons/record.svg"
import Card from "../widgets/Card/Card";
import {HapticAlert} from "../shared/HapticAlert/HapticAlert";
import {History} from "../shared/History/History";
import AnimatedBox from "../features/AnimationSlider/AnimationSlider";


function App() {

    let tg = (window as any).Telegram.WebApp


    const [page, setPage] = useState("main")
    const [statusContact, setStatusContact] = useState<string>()

    const backButton = () => {
        setPage("main")
    }
    const visibleButton = () => {
        tg.BackButton.show()
        tg.BackButton.onClick(backButton)

    }

    useEffect(() => {
        if (page === "main") {
            tg.BackButton.hide()
        } else {
            visibleButton()
        }

    }, [page]);
    const openCard = () => {
        setPage("card")
    }
    const testContact =(window as any).Telegram.WebApp.onEvent('contactRequested', (e:any)=>{return e})

    const openForm = () => {

        tg.requestContact([])
        (window as any).Telegram.WebApp.onEvent('contactRequested', function (params:any){
              setStatusContact(`${JSON.stringify(params)}`)



        })
        // setPage("form")

    }
    const openHistory = () => {
        setPage("form")

        // setPage("history")

    }

    const openChat = () => {
        tg.openTelegramLink("https://t.me/RsAutoPro")
    }
    return (
        <>
            <div style={{color:"yellow"}}>{JSON.stringify(testContact)} проверка3</div>
            {page === "form" && <Form/>}
            {page === "main" &&
                <MainPage openChat={openChat} isCard={openCard}>
                    <HapticAlert>
                        <div onClick={openForm} className={cls.button}>
                            <img src={record} alt="Record"/>
                            <p>Запись на прием</p>
                        </div>
                    </HapticAlert>
                    <HapticAlert messageAlert={"Запись удалена"}>
                        <div className={cls.button}>
                            <img src={minus} alt="Minus"/>
                            <p>Отмена записи</p>
                        </div>
                    </HapticAlert>
                    <HapticAlert>
                        <div onClick={openHistory} className={cls.button}>
                            <img src={menu} alt="Menu"/>
                            <p>История записей</p>
                        </div>
                    </HapticAlert>
                </MainPage>
            }
            {page === "card" && <Card/>}
            {page === "history" && <History/>}

            <AnimatedBox/>


        </>


    );
}

export default App;
