import React, {memo, useEffect, useState} from "react";
import DatePicker from "react-mobile-datepicker";


interface DateScrollPickerProps {
    className?: string
    onTimeChange: (newTime: Date) => void
    onSelect: () => void

}

const DateScrollPicker = memo((props: DateScrollPickerProps) => {
    let tg = (window as any).Telegram.WebApp
    const {
        onTimeChange,
        onSelect
    } = props
    const [time, setTime] = useState<Date>(new Date());
    const [isOpen, setIsOpen] = useState<boolean>(true);

    const handleClick = () => {
        setIsOpen(!isOpen);
    };
    useEffect(() => {
        handleClick()

    }, [time]);
    const haptic = (time: Date) => {
        const currentTime = new Date();

        // Проверяем, является ли выбранная дата временем в прошлом
        if (time < currentTime) {
            // Если выбранное время меньше текущего времени, устанавливаем текущую дату
            setTime(currentTime);
            onTimeChange(currentTime);
        } else {
            // Иначе используем выбранное время
            setTime(time);
            onTimeChange(time);
        }

        tg.HapticFeedback.selectionChanged()
    }
    const handleCancel = () => {
        setIsOpen(false);
        setTime(new Date())
        onTimeChange(time)
        tg.HapticFeedback.impactOccurred("light")
    };

    const handleSelect = (time: Date) => {
        const specifiedTime = new Date();
        specifiedTime.setHours(20, 0, 0, 0); // Устанавливаем 20:00 как время сравнения

        if (time.getHours() > 20 || (time.getHours() === 20 && time.getMinutes() > 0)) {
            // Время больше 20:00
            const message = "Максимальное время записи 20:00"
            tg.showAlert(message, [])
            setTime(time);
            onTimeChange(time);
        } else {
            setTime(time);
            onTimeChange(time);

        }

        setIsOpen(false);
        onSelect();
    };

    interface MonthData {
        [key: number]: string;
    }

    const monthMap: MonthData = {
        1: 'Январь',
        2: 'Февраль',
        3: 'Март',
        4: 'Апрель',
        5: 'Май',
        6: 'Июнь',
        7: 'Июль',
        8: 'Август',
        9: 'Сентябрь',
        10: 'Октябрь',
        11: 'Ноябрь',
        12: 'Декабрь',
    };

    const configDate = {
        'month': {
            format: (value: Date) => monthMap[value.getMonth() + 1],
            caption: 'Mon',
            step: 1,
        },
        'date': {
            format: 'DD',
            caption: 'Day',
            step: 1,
        },
        'hour': {
            format: 'hh',
            caption: 'Hour',
            step: 1,
        },
        'minute': {
            format: 'mm',
            caption: 'Min',
            step: 1,
        },
    };
    return (
        <div>
            {/*<button onClick={handleClick}>open</button>*/}
            <DatePicker

                value={time}
                isOpen={true}
                onSelect={handleSelect}
                onCancel={handleCancel}
                isPopup={false}
                showHeader={false}
                dateConfig={configDate}
                onChange={haptic}
                confirmText="Готово"
                cancelText="Сбросить"
                theme={"ios"}
            />

        </div>
    );
});
export default DateScrollPicker;
