import React, {MutableRefObject, useEffect, useRef} from 'react';
import cls from "./AnimationSlider.module.scss"

const AnimatedBox = () => {
    const boxRef = useRef() as MutableRefObject<HTMLDivElement>
    useEffect(() => {
        const observer = new IntersectionObserver((entries) => {
            entries.forEach((entry) => {
                const intersectionRatio = entry.intersectionRatio; // Процент видимости объекта

                const opacity = 1 - intersectionRatio; // Вычисляем прозрачность
                const widthPercentage = 100 - intersectionRatio * 100; // Вычисляем ширину в процентах

                boxRef.current.style.opacity = opacity.toString();
                boxRef.current.style.width = `${widthPercentage}%`;
            });
        });

        observer.observe(boxRef.current);

        return () => observer.unobserve(boxRef.current);
    }, []);

    return <div className={cls.wrapper}>
        <div style={{width:"200px", height:"100px" ,backgroundColor:"red"}}></div>
        <div style={{width:"200px", height:"100px" ,backgroundColor:"red"}}></div>
        <div style={{width:"200px", height:"100px" ,backgroundColor:"red"}}></div>
        <div style={{width:"200px", height:"100px" ,backgroundColor:"red"}}></div>
        <div ref={boxRef} className={cls.AnimationSlider}></div>
    </div>
        ;
};

export default AnimatedBox;
