import React, {memo, ReactNode, useEffect, useState} from 'react';
import {Box, Button, FormControl, InputLabel, MenuItem, Select, Slide, TextField} from "@mui/material";
import moment from "moment/moment";
import cls from "./Form.module.scss";
import DateScrollPicker from "../SelectDate/SelectDate";
import {HapticAlert} from "../HapticAlert/HapticAlert";

interface FormProps {
    className?: string
    children?: ReactNode
}


export const Form = memo((props: FormProps) => {
    const {
        className,
        children,
        ...otherProps
    } = props

    let tg = (window as any).Telegram.WebApp
    const [sendButton, setSendButton] = useState(false)
    const [visibleSelectDate, setVisibleSelectDate] = useState(false)
    type ApointmentData = {
        username: string;
        date: Date;
        numberPhone: any;
        brandCar: string;
        modelCar: string;
        yearOfIssue: string;
        service: string;
    };
    const [apointmentData, setApointmentData] = useState<ApointmentData>({
        username: `${JSON.stringify(tg?.initData)}`,
        date: new Date(),
        numberPhone: "+7 (900) 000 - 00 - 00",
        brandCar: "",
        modelCar: "",
        yearOfIssue: "0000",
        service: "Другое"
    });


    useEffect(() => {
        if (
            apointmentData?.username.trim() === "" ||
            apointmentData?.numberPhone.trim() === "" ||
            apointmentData?.brandCar.trim() === "" ||
            apointmentData?.modelCar.trim() === "" ||
            apointmentData?.yearOfIssue.trim() === "0000" ||
            (apointmentData?.date.getHours() > 20 || (apointmentData?.date.getHours() === 20 && apointmentData?.date.getMinutes() > 0))
        ) {
            // Один из полей не заполнен
            // setSendButton(false)
        } else {
            setSendButton(true)

            // Все поля заполнены
            // Здесь можно добавить другую логику при успешном заполнении полей
        }
    }, [apointmentData]);
    const handleTimeChange = (time: any) => {
        setApointmentData({...apointmentData, date: time})
    };
    const hapticAlert = () => {
        const messageAlert = "Данные отправлены"
        tg.HapticFeedback.impactOccurred("light")
        tg.showAlert(messageAlert, [])
        tg.sendData(apointmentData)
    }


    const onSelect = () => {
        setVisibleSelectDate(false)

    }
    useEffect(() => {
        console.log(apointmentData.numberPhone < 18)
    }, [apointmentData.numberPhone]);

    const handlePhoneNumberChange = (e: React.ChangeEvent<HTMLInputElement>) => {


        let formattedPhoneNumber = e.target.value;

        // Удаляем все символы, кроме цифр
        formattedPhoneNumber = formattedPhoneNumber.replace(/\D/g, '');

        // Применяем маску к номеру телефона
        if (formattedPhoneNumber.length <= 1) {
            if (formattedPhoneNumber === "7" || "8") {
                formattedPhoneNumber = `+7 (${formattedPhoneNumber.slice(1, 4)}`
            } else {
                formattedPhoneNumber = `+7 (${formattedPhoneNumber}`;
            }
        } else if (formattedPhoneNumber.length <= 4) {
            formattedPhoneNumber = `+7 (${formattedPhoneNumber.slice(1, 4)}`;
        } else if (formattedPhoneNumber.length <= 7) {
            formattedPhoneNumber = `+7 (${formattedPhoneNumber.slice(1, 4)}) ${formattedPhoneNumber.slice(4, 7)}`;
        } else if (formattedPhoneNumber.length <= 9) {
            formattedPhoneNumber = `+7 (${formattedPhoneNumber.slice(1, 4)}) ${formattedPhoneNumber.slice(4, 7)}-${formattedPhoneNumber.slice(7, 9)}`;
        } else {
            formattedPhoneNumber = `+7 (${formattedPhoneNumber.slice(1, 4)}) ${formattedPhoneNumber.slice(4, 7)}-${formattedPhoneNumber.slice(7, 9)}-${formattedPhoneNumber.slice(9, 11)}`;
        }

        setApointmentData({...apointmentData, numberPhone: formattedPhoneNumber});
    };

    const SELECT_TO = [
        "Ремонт глушителя",
        "Ремонт ходовой",
        "Ремонт двигателя",
        "Шиномонтаж",
        "Автоэлектрика",
        "ТО автомобиля",
        "Ремонт тормозной системы",
        "Ремонт турбины",
        "Ремонт рулевой рейки",
        "Авто-кондиционер",
        "Перетяжка руля",
        "ЧИП Тюнинг",
        "Антикор",
        "Другое"
    ]


    return (
        <div className={cls.Form}
        >
            <Box
                sx={{
                    borderRadius: "10px",
                    width: "70vw",
                    margin: "0 auto",
                    bgcolor: 'background.paper',
                    p: 4,

                }}
            >
                <div>
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="small"
                        helperText=""
                        id="outlined"
                        value={`${tg.initDataUnsafe.user.first_name}  ${tg.initDataUnsafe.user.last_name}`}
                        label="Имя Фамилия"
                        onClick={() => setVisibleSelectDate(false)}
                        onChange={(e) =>
                            setApointmentData({...apointmentData, username: e.target.value})}
                    />
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={apointmentData.numberPhone.length < 18}
                        fullWidth
                        size="small"
                        helperText={apointmentData.numberPhone.length < 18 ? "Введите полностью номер телефона" : ""}
                        id="outlined"
                        label="+7 (900) 000 - 00 - 00"
                        type={"tel"}
                        onClick={() => {
                            setApointmentData({...apointmentData, numberPhone: ""});
                            setVisibleSelectDate(false)
                        }}
                        value={apointmentData.numberPhone}
                        onChange={handlePhoneNumberChange}
                    />
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="small"
                        helperText=""
                        id="outlined"
                        label="Марка"
                        onClick={() => setVisibleSelectDate(false)}
                        onChange={(e) =>
                            setApointmentData({...apointmentData, brandCar: e.target.value})}
                    />
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="small"
                        helperText=""
                        id="outlined"
                        label="Модель"
                        onClick={() => setVisibleSelectDate(false)}
                        onChange={(e) =>
                            setApointmentData({...apointmentData, modelCar: e.target.value})}
                    />
                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={apointmentData?.yearOfIssue.length < 4}
                        fullWidth
                        size="small"
                        helperText={apointmentData?.yearOfIssue.length < 4 ? "Введите год в формате 1900" : ""}
                        id="outlined"
                        label="Год выпуска"
                        type={"tel"}
                        inputProps={{
                            maxLength: 4 // Ограничить ввод четырьмя символами
                        }}
                        onClick={() => setVisibleSelectDate(false)}
                        onChange={(e) =>
                            setApointmentData({...apointmentData, yearOfIssue: e.target.value})}
                    />
                    <FormControl
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}

                        fullWidth size="small">
                        <InputLabel id="demo-select-small-label">Выбор услуги</InputLabel>
                        <Select
                            labelId="demo-select-small-label"
                            id="demo-select-small"
                            value={apointmentData.service}
                            label="Выбор услуги"
                            onChange={(e) =>
                                setApointmentData({...apointmentData, service: e.target.value})}

                        >
                            {SELECT_TO.map((item, index) => (
                                <MenuItem key={index} value={item}>{item}</MenuItem>
                            ))}
                        </Select>
                    </FormControl>

                    <TextField
                        sx={{
                            '& .MuiTextField-root': {m: 1, width: '25ch'},
                            marginBottom: "10px"
                        }}
                        error={false}
                        fullWidth
                        size="small"
                        helperText=""
                        id="outlined"
                        label="Дата приема"
                        value={
                            `${moment(apointmentData.date).format("DD/MM/YYYY HH:mm")}`
                        }
                        onClick={() => setVisibleSelectDate(true)}
                        inputProps={{readOnly: true}}
                    />
                </div>
                <div className={cls.sendButton}>
                    <Slide
                        direction="right" in={sendButton} mountOnEnter unmountOnExit>
                        <Button
                            onClick={hapticAlert}
                            sx={{margin: "10px auto 10px auto"}}
                            variant="contained"
                            size="large"
                            disableElevation
                        >
                            Отправить
                        </Button>


                    </Slide>

                </div>

            </Box>
            <div
                className={visibleSelectDate ? `${cls.selectDate} ${cls.openSelect}` : `${cls.selectDate} ${cls.closeSelect}`}
            >
                <DateScrollPicker onSelect={onSelect} onTimeChange={handleTimeChange}/>
            </div>

        </div>
    );
});